.menu {
  &-enabled {
    width: 20%;
    transition: width 0.3s ease-in-out;
    height: 94.1vh;
    background-color: #cbd2c3;

    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 9998;

    overflow: scroll;
  }
}

.content {
  &-header {
    font-weight: bolder;
    font-family: Lato;
    font-size: 25px;
    text-align: center;

    color: #47525e;
    cursor: default;
  }

  &-divider {
    width: 80%;
    border-bottom: 2px solid #8e9a84;
    margin: auto;
    margin-top: 20px;
  }

  &-items {
    margin-top: 10%;
  }

  &-hide {
    display: none;
  }
}

.finish-in-menu {
  border-radius: 10px;
  border: 1px solid #8492a6;

  background-color: #b0b6a9;
  color: #343f4b;

  font-family: Lato;
  font-weight: bolder;

  height: 35px;
  width: 90px;

  margin-left: 5%;
  margin-left: 35%;

  margin-top: 10%;

  position: relative;
}

